<template>
	<div class="pa-md-6 pa-sm-3">
		<v-card>
			<v-card-title class="py-2">Daftar Incubator</v-card-title>
			<v-card-text>
				<Loader v-if="isLoading" />
				<v-data-table
					v-else
					class="mt-0"
					:headers="header"
					:items="dataIncubator"
					:items-per-page="10">	
					<template v-slot:item.no="{item}">
						{{dataIncubator.map(x => x.id).indexOf(item.id)+1}}
					</template>
					<template v-slot:item.incubator="{item}">
						<div class="d-flex align-center">
							<!-- <v-avatar size="40">
								<img :src="require('@/assets/'+item.incubator.logo)" alt="logo incubator">
							</v-avatar> -->
							<div class="ml-3">{{ item.name }}</div>
						</div>
					</template>
					<template v-slot:item.manage="{item}">
						<div class="d-flex align-center">
							<!-- <v-avatar size="40">
								<img :src="require('@/assets/'+item.manage.avatar)" alt="logo manage">
							</v-avatar> -->
							<div class="ml-3">{{ item.managed_by }}</div>
						</div>
					</template>
					<template v-slot:item.aktifasi="{ item }">
						<v-btn 
							style="border-radius: 2px 0 0 2px"
							x-small
							@click="changeAktifasi({
								id: item.id,
								value: 1
							})"
							:loading="isLoadingAktifasi"
							:color="item.aktifasi === '1'?'success':'default'"
							depressed>
							on
						</v-btn>
						<v-btn 
							style="border-radius: 0 2px 2px 0"
							x-small
							@click="changeAktifasi({
								id: item.id,
								value: 0
							})"
							:loading="isLoadingAktifasi"
							:color="item.aktifasi === '0'?'red':'default'"      	
							:dark="item.aktifasi === false?true:false" 
							depressed>
							off
						</v-btn>
					</template>
					<template v-slot:no-data>
						<v-alert type="info" class="mt-4">
							<div>Data tidak ditemukan</div>
						</v-alert>
					</template>
					<template v-slot:item.action="{}">
						<v-btn icon color="primary">
							<v-icon small>mdi-eye</v-icon>
						</v-btn>
						<v-btn icon color="red">
							<v-icon small>mdi-trash-can</v-icon>
						</v-btn>
					</template>		
				</v-data-table>
			</v-card-text>
		</v-card>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Incubator', value: 'name', sortable: false },
				{ text: 'Status', value: 'status', sortable: false },
				{ text: 'Managed By', value: 'managed_by', sortable: false },
				{ text: 'Aktifasi', value: 'aktifasi', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					id: 1,
					incubator: {
						logo: 'avatar.jpg',
						name: 'Amikom Bussniess Park'
					},
					status: 'Premium',
					manage: {
						avatar: 'avatar.jpg',
						name: 'Rizky Rosyid Hidayat'
					},
					aktivasi: true
				}
			],
		}),

		components: {
			Loader
		},

		created() {
			this.$store.dispatch('dataIncubator/getDataIncubator')
			this.$store.dispatch('dataIncubator/updateIsLoading', true)
		},

		computed: {
			...mapState('dataIncubator', ['isLoading', 'dataIncubator', 'isLoadingAktifasi'])
		},

		methods: {
			changeAktifasi(val) {
				this.$store.dispatch('dataIncubator/postAktifasiIncubator', {
					id: val.id,
					aktifasi: val.value
				})
				this.$store.dispatch('dataIncubator/updateIsLoadingAktifasi', true)
			}
		}
	}
</script>